h1,h2,h3,h4,h5,h6,p{
    margin: 0;

}
ul,ol{
    margin: 0;
    padding: 0;
    list-style: none;
}
img{
    display: block;
    max-width: 100%;
    height: auto;
}
a{
    text-decoration: none;
}
body{
    background-color: var(--fill-back);
    font-family: var(--primery-font);
}
button{
    padding: 0;
    cursor: pointer;
    background-color: transparent;
    border: none;
}